@import url(https://fonts.googleapis.com/css2?family=Special+Elite&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poiret+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Special+Elite&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poiret+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Staatliches&display=swap);
a:-webkit-any-link {
    text-decoration: none;
    color: inherit;
  }
body{
  background-color: #C9D8B6;
}
.HomePage .Subtitle{font-size:2.5rem;text-align:left;font-family:"Special Elite",cursive;background-color:#787a91;padding:1px 0px 1px 30px;justify-items:center}.HomePage .Subtitle h4{margin:15px}.HomePage .genres{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr 1fr;grid-column-gap:2px;grid-row-gap:2px}.HomePage .genres .genre{font-size:3rem;padding:40px 20px 50px 20px;color:#fff;background-color:#141e61;font-family:"Poiret One",cursive;border:1px solid #eee;border-radius:2%;box-shadow:4px 4px #787a91;font-weight:bold}.HomePage .genres .genre:hover{font-size:3rem;padding:40px 20px 50px 20px;color:#fff;background-color:#3c5186;font-family:"Poiret One",cursive;border:1px solid #eee;border-radius:2%;box-shadow:4px 4px #787a91;font-weight:bold}
.YearPage .Subtitle{font-size:2.5rem;text-align:left;font-family:"Special Elite",cursive;background-color:#787a91;padding:1px 0px 1px 30px;justify-items:center}.YearPage .Subtitle h4{margin:15px}.YearPage .years{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr 1fr;grid-column-gap:2px;grid-row-gap:2px}.YearPage .years .year{font-size:3rem;padding:40px 20px 50px 20px;color:#fff;background-color:#141e61;font-family:"Poiret One",cursive;border:1px solid #eee;border-radius:2%;box-shadow:4px 4px #787a91;font-weight:bold}.YearPage .years .year:hover{font-size:3rem;padding:40px 20px 50px 20px;color:#fff;background-color:#3c5186;font-family:"Poiret One",cursive;border:1px solid #eee;border-radius:2%;box-shadow:4px 4px #787a91;font-weight:bold}
.MoviesPage .Subtitle{font-size:2.5rem;text-align:left;font-family:"Special Elite",cursive;background-color:#787a91;padding:1px 0px 1px 30px;justify-items:center}.MoviesPage .Subtitle h4{margin:15px}.MoviesPage .movies{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr 1fr;grid-column-gap:2px;grid-row-gap:2px}.MoviesPage .movies .movie{font-size:3rem;padding:40px 20px 50px 20px;color:#fff;background-color:#141e61;font-family:"Poiret One",cursive;border:1px solid #eee;border-radius:2%;box-shadow:4px 4px #787a91;font-weight:bold}
.App{text-align:center;grid-template-columns:1fr 2fr}.App .Title{font-size:2rem;font-family:"Staatliches",cursive;letter-spacing:10px;background-color:#0f044c;color:#fff;padding:10px}
